<template lang="pug">
  div.addfriend
    hd-bar.messages-hd-bar( title-name="添加" )
      div( slot="sec-l", @click="$router.go(-1)" ) 
        span.icon.iconfont.icon-fanhui 
        span 联系人
    div.ui-list.ui-list-single.ui-border-tb( @click="toSearchFriend" )
      li.ui-border-t
        .ui-list-info
          span.ui-nowrap QQ号
    searchUser.search-friend-view( v-show="isSearch")
       
        
</template>
<script>
import hdBar from '@/components/hd-bar'
import searchUser from './searchuser'
export default {
  components: { hdBar, searchUser },
  data() {
    return {
      isSearch: true,
      transitionName: ''
    }
  },
  methods: {
    toSearchFriend() {
      this.isSearch = true;
    }
  },
}
</script>
<style lang="stylus" scoped>
  .addfriend 
    padding-top $hd-bar-height 
    height 100vh
    .addfriend-wrapper 
    .search-friend-view 
      transform translateY(0rem)  
      position absolute 
      top 0 
      left 0
      width 100% 
      height 100vh
      z-index 70
      background #f2f2f2
      opacity 1
   
      


</style>
<style lang="stylus">

</style>






